*{
    margin: 0;
    padding: 0;
    background-color: black;
    color: aqua;
}

.Heading {
 text-align: center;
 background-color: rgb(73, 8, 135);
 padding: 50px;
 margin: 40px;
 font-style: italic;
 font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
}

ol li {
    list-style: decimal;
    list-style-position: inside;
    font-size: 25px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    margin-bottom: 10px;

}